.App {
  width: 100%;
  text-align: center;
  margin:0 auto;
}

.App-logo {
 /* animation: App-logo-spin infinite 20s linear; */
  height: 80px;
  position: relative;
}

.App-header {
  vertical-align: top;
  position: relative;
  text-align: left;
  margin: 0 auto;
  background-color: white;
  height: 150px;
  width: 980px;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.main_menu {
  display: inline-block;
}

.Mainpage {
  width: 980px;
  min-height: calc(77vh - 140px);
  padding-bottom: 20px;
  margin: 0 auto;
  
}

.logo {
  display: inline-block;
  margin-right: 200px;
}

.topMenuTextStyle {
  display: table-cell;
  font-size: 19px;
  font-weight: bold;
  cursor: pointer;
  color: #9e7102;
  letter-spacing: -0.5px;
  width: 270px;
}

a {
    color: #7d7d7d;
    text-decoration: none;
}

