.Footer {
    width: 100%;
    height: 140px;
    text-align: left;
    background: #3a3a3a;
}

.footer_content {
	padding: 10px 0;
    margin: 0 auto;
    width: 980px;
}

.footer_logo {
	padding: 10px 0;
	display: inline-block;
	float: left;
}
.footer_info {
    float: left;
    width: 680px;
    margin-left: 110px;
    font-family: NanumGothic, 나눔고딕;
    font-size: 12px;
    color: rgb(124, 123, 123);
    line-height: 20px;
}
.footer_info .footer_navi {
    padding: 0px;
}
.footer_info .footer_address {
    margin-top: 7px;
    font-size: 12px;
    line-height: 20px;
}

li {
	list-style-type: none;
}


